import React from 'react';

import './namepath.styles.scss';

class NamePath extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      text: ''
    }
  }
  pathsvg = text => (
    <svg className='name-svg' viewBox="0 -65 650 650">
      <path id="curve" d="M73.2,148.6c4-6.1,65.5-96.8,178.6-95.6c111.3,1.2,170.8,90.3,175.1,97" />
      <text width="650">
        <textPath className='text-curve' startOffset="50%" href="#curve">
          { text }
    </textPath>
      </text>
    </svg>
  );

  render = () => this.pathsvg(this.props.text);
}

export default NamePath;