import React from 'react';

import './ring-section.styles.scss';

class RingSection extends React.Component {
  constructor(props) {
    super(props);

    this.radius = 177;
    this.stroke = 55;
    this.normalizedRadius = this.radius - this.stroke * 2;
    this._circumference = this.normalizedRadius * 2 * Math.PI;
    this.arc = (this._circumference/this.props.arc) - 2;
    this.space = this._circumference - this.arc;
  }

  render = () => {
    const rotate = `rotate(${this.props.rotation})`;
    const dasharray = `${this.arc} ${this.space}`
    return (
      <div className='inner-ring'>
        <svg
          className='inner-ring__svg'
          height={(this.radius * 2)}
          width={(this.radius * 2)}
        >
          <circle
            className='circle'
            stroke="white"
            strokeDasharray={dasharray}
            strokeDashoffset={this._circumference}
            transform={rotate}
            strokeOpacity="30%"
            strokeWidth={this.stroke}
            fill="transparent"
            r={this.normalizedRadius}
            cx={this.props.cx}
            cy={this.props.cy}
          />
        </svg>
      </div>
    )
  }
}

export default RingSection;