import React from 'react';
import Name from '../name/name.component';
import Indicator from '../indicator/indicator.component';

import './ring.styles.scss';

class Ring extends React.Component {
  constructor() {
    super();

    this.state = {
      text: 'The App Name',
    }
  }

  render() {
    return (
      <div className="ring">
        <Name text={ this.state.text } />
        <div className='indicators'>
          <Indicator className='indicator' stroke='15' radius='142' rotation="0" type='lit' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="45" type='lit' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="90" type='unlit' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="135" type='unlit' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="180" type='warning' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="225" type='warning' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="270" type='lit' />
          <Indicator className='indicator' stroke='15' radius='142' rotation="315" type='lit' />
        </div>
        
      </div>
    )
  }

}

export default Ring;