import React from 'react';


import './indicator.styles.scss';

class Indicator extends React.Component {
  constructor(props) {
    super(props);

    this.normalizedRadius = this.props.radius - this.props.stroke * 2;
    this._circumference = this.normalizedRadius * 2 * Math.PI;
    this.arc = (this._circumference/8) - 3;
    this.space = this._circumference - this.arc;
  }
  render = () => {
    let stroke = '#6a8698';
    switch(this.props.type) {
      case 'lit':
        stroke = '#b9c6ce';
      break;
      case 'warning':
        stroke = '#c42f2f';
      break;
      case 'unlit':
      default:
        stroke = '#6a8698'


    }
    const rotate = `rotate(${this.props.rotation})`;
    const dasharray = `${this.arc} ${this.space}`
    return (
      <svg
        height={(this.props.radius * 2)}
        width={(this.props.radius * 2)}
      >
        <circle
          className='circle'
          stroke={stroke}
          strokeDasharray={dasharray}
          strokeDashoffset={this._circumference}
          transform={rotate}
          transformOrigin="50% 50%"
          strokeWidth={this.props.stroke}
          fill="transparent"
          r={this.normalizedRadius}
          cx={this.props.radius}
          cy={this.props.radius}
        />
      </svg>
    )
  };
}

export default Indicator;